.content-home {
    h1 {
        font-size: 6rem;
        font-family: 'Bebas Neue';
        letter-spacing: 0.5rem;
        line-height: 6rem;
        transition: ease-out all 1s;
        margin-bottom: 5px;
        opacity: 0;

        @media (max-width: 768px) { 
            line-height: 5.3rem;
            font-size: 5rem;
        }
    }

    h2 {
        font-family: 'Helvetica';
        src: url('../../../assets/helvetica-light.ttf');
        transition: ease-out all 1s;

        opacity: 0;
        @media (max-width: 768px) { 
            font-size: 2rem;
        }
    }

    h3 {
        font-size: 1rem;
        padding-bottom: 2rem;
        transition: ease-out all 1s;

        opacity: 0;
    }

    p {
        transition: ease-out all 1s;
        opacity: 0;
    }
    
    .content-buttons {
        transition: ease-out all 1s;
        opacity: 0;
    }

    @media (max-width: 768px) {
        width: 75%;
    }
    
}