body {
    overflow: hidden;
}

button {
    padding: 10px 20px;
    margin-right: 10px;
    font-weight: 600;

    transition: all .2s ease-out;
}

button.light {
    background-color: #fff;
    border: 2px solid #000;

    &:hover {
        background-color: #000;
        border: 2px solid #fff;
        color: #fff;
    }
}

button.dark {
    background-color: #000;
    border: 2px solid #fff;
    color: #fff;

    &:hover {
        background-color: #fff;
        border: 2px solid #000;
        color: #000;
    }
}